import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader.js';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';

/**
 * Base
 */


// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Textures
 */
const textureLoader = new THREE.TextureLoader()

textureLoader.load('https://images.unsplash.com/photo-1579546929518-9e396f3cc809?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8Z3JhZGllbnR8ZW58MHx8MHx8&w=1000&q=80' , function(texture)
            {
             scene.background = texture;  
            });



const loader = new FontLoader();

loader.load( 'fonts/helvetiker_regular.typeface.json', ( font )=> {

	const textGeometry = new TextGeometry(`
      Yakshit Garg
     Front-End Dev
    `,
      {
		font: font,
		size: 0.7,
		height: 0.2,
		curveSegments: 8,
		bevelEnabled: true,
		bevelThickness: 0.05,
		bevelSize: 0.03,
		bevelOffset: 0,
		bevelSegments: 4
	} )
    // textGeometry.computeBoundingBox()
    // textGeometry.translate(
    //     - (textGeometry.boundingBox.max.x-0.02)*0.5, 
    //     - (textGeometry.boundingBox.max.y-0.02)*0.5,
    //     - (textGeometry.boundingBox.max.z-0.03)*0.5
    // )
    textGeometry.center()
    const material = new THREE.MeshNormalMaterial({flatShading: true})
    const text = new THREE.Mesh(textGeometry, material)
    scene.add(text)
    const donutGeometry = new THREE.TorusBufferGeometry(0.3,0.2,20,45)
    
    for (let i = 0; i < 250; i++){
        
        const donut  = new THREE.Mesh(donutGeometry, material)
        donut.position.x = (Math.random()-0.5)*21
        donut.position.y = (Math.random()-0.5)*21
        donut.position.z = (Math.random()-0.5)*21
        donut.rotation.x = (Math.random()*Math.PI)
        donut.rotation.y = (Math.random()*Math.PI)
        const scale = Math.random()
        donut.scale.set(scale,scale,scale)
        scene.add(donut)
        
    }
    const cubeGeometry = new THREE.BoxBufferGeometry(1,1)
    
    for (let i = 0; i < 250; i++){

        const cube  = new THREE.Mesh(cubeGeometry, material)
        cube.position.x = (Math.random()-0.5)*21
        cube.position.y = (Math.random()-0.5)*21
        cube.position.z = (Math.random()-0.5)*21
        cube.rotation.x = (Math.random()*Math.PI)
        cube.rotation.y = (Math.random()*Math.PI)
        const scale = Math.random()
        cube.scale.set(scale,scale,scale)
        scene.add(cube)
        
    }
} );

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 4
camera.position.y = 0
camera.position.z = 5
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.minDistance = 2;
controls.maxDistance = 9;

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>
{
 

    // Update controls
    controls.update()
    
    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()